.header {
	position: sticky;
	top: 0;
	height: 80px;
	background-color: #fff;
	color: #222;
	font-size: 0.875rem;
	font-weight: 600;
	display: flex;
	align-items: center;
	justify-content: space-between;
	padding: 0 5rem;
	border-bottom: 1px solid #ebebeb;
	z-index: 1;
}

.header--logo {
	display: none;
}

@media (min-width: 768px) {
	.header--logo {
		display: inline-flex;
		object-fit: contain;
		width: 120px;
		cursor: pointer;
	}
}

.header--middle {
	position: relative;
	display: flex;
	align-items: center;
	justify-content: center;
	border: 1px solid #e0e0e0;
	border-radius: 9999px;
	box-shadow: 0 4px 6px -1px rgb(0 0 0 / 0.1), 0 2px 4px -2px rgb(0 0 0 / 0.1);
	--tw-shadow-color: #9ca3af;
	height: 2.5rem;
	width: 21rem;
	padding: 0.25rem 0.5rem;
	cursor: pointer;
}

.header--middle:hover {
	box-shadow: 0 6px 8px -1px rgb(0 0 0 / 0.1), 0 2px 4px -2px rgb(0 0 0 / 0.1);
	--tw-shadow-color: #9ca3af;
}

.header--middle > input {
	border: none;
	border-radius: 9999px;
	outline-width: 0;
}

.header--middleButtons {
	position: absolute;
	display: flex;
	justify-content: space-evenly;
	align-items: center;
}

.header--middleButton {
	height: 1.5rem;
	background-color: #fff;
	color: #484848;
	font-size: 0.875rem;
	font-weight: 600;
	border: none;
	padding: 0 1rem;
	border-right: 1px solid #e9e9e9;
	cursor: pointer;
}

.header--middleButtonGuests {
	background-color: #fff;
	border: none;
	font-size: 0.875rem;
	font-weight: 400;
	color: #717171;
	padding: 0 0.75rem;
	cursor: pointer;
}

.header--middleSearchIcon {
	width: 0.75rem;
	height: 0.75rem;
	display: inline-block;
	background-color: #ff385c;
	color: #fff;
	padding: 0.625rem;
	border-radius: 9999px;
	cursor: pointer;
}

.header--right {
	display: none;
}

@media (min-width: 768px) {
	.header--right {
		display: flex;
		align-items: center;
		justify-content: space-around;
		white-space: nowrap;
	}
}

.header--right > p {
	padding: 0.75rem 1rem;
	border-radius: 9999px;
	cursor: pointer;
}

.header--right > p:hover {
	background-color: #f7f7f7;
}

.header--globeIcon {
	height: 1rem;
	width: 1rem;
	padding: 0.75rem;
	border-radius: 9999px;
	cursor: pointer;
}

.header--rightMenuOption:hover,
.header--globeIcon:hover {
	background-color: #f7f7f7;
}

.header--userMenu {
	position: absolute;
	top: 70px;
	right: 0px;
	display: flex;
	flex-direction: column;
	align-items: flex-start;
	width: 200px;
	background-color: #fff;
	color: #222;
	padding: 5px 15px;
	border: 1px solid #efefef;
	border-radius: 10px;
	z-index: 1;
}

.header--rightMenuOption {
	margin: 0;
	padding: 0.75rem;
	border-radius: 9999px;
	cursor: pointer;
}

.header--userMenuSignUp {
	font-weight: 600;
}

.header--userMenu > hr {
	height: 1px;
	width: 100%;
	border: none;
	background-color: #ebebeb;
}

.header--userMenuIcons {
	margin-left: 5px;
}

.header--userMenuIcons:hover {
	box-shadow: 0 6px 8px -1px rgb(0 0 0 / 0.1), 0 2px 4px -2px rgb(0 0 0 / 0.1);
	--tw-shadow-color: #9ca3af;
	transition: 0.2s;
}

.header--userIcon {
	width: 2rem;
	height: 2rem;
}
